/* STYLE 01
 * header structure style 01
 * inlcusion for: _header.scss str: 42
 * & = .main-header 
 * very simple structure:
       [logo]
       [menu]
 */

$_header_bg_color: rgba(0,0,0,.85);
$_header_bg_color_2: transparent;

.main-header {
    @include background-gradient($_header_bg_color, $_header_bg_color_2, 'vertical');
    position: relative;

    &:after {
        content: '';
        display: block;
        width: 100%;
        height: 0;
        position: absolute;
        top: 0;
        left: 0;
        transition: background .3s, height .3s;
        background: $_header_bg_color_2;
        z-index: 1;

        @include is_body_class(scrolled) {
            background: $_color_txt;
            height: 100%;
        }
    }

    &:hover {
        &:after {
            background: $_color_txt;
            height: 100%;
        }
    }



    svg#olga_logo {
        .cls-1 {
            fill:#fff;
            transition: fill .3s;

            @include is_body_class(scrolled) {
                fill:#fff;
            }
        }
    }

	&__nav-wrapper {
        padding: 0 15px;
        @include max-width(calc(100% - 80px));
        @include flexme;
        position: relative;
        z-index: 200;

        @include screen(custom, max, 1510) {
            max-width: 100%;
        }

        @include screen(custom, max, 675) {
            width: 100%;
            max-width: 100%;
        }

        @include screen(custom, max, 320) {
            padding: 0 5px;
        }
	}

    &__logo {

        @include screen(custom, max, $_mobile_menu_toggle_screen_size) {
            order: 1;
            flex: 1;
        }

        &__link {
            display: block;
            padding: 10px 15px;

            @include screen(custom, max, 424) {
                padding: 10px 0;
            }

            .img {
                width: 100%;
                
                @include screen(custom, max, 675) {
                    width: 150px !important;
                }

                @include screen(custom, max, 424) {
                    width: 130px !important;
                }

                svg {
                    width: 100%;
                    transition: width .3s;

                    @include is_body_class(scrolled) {
                        width: 100% !important;
                    }

                    @include screen(custom, max, 675) {
                        width: 100% !important;

                        @include is_body_class(scrolled) {
                            width: 100% !important;
                        }
                    }


                }
                
            }
        }
    }

    &__contact {

        @include screen(custom, max, $_mobile_menu_toggle_screen_size) {
            order: 2;
        }

        .site_contact_phone {
            & > a {
                @extend .buttonme;

                @include screen(custom, max, 424) {
                    padding: 8px 5px;

                    i {
                        display: none;
                    }
                }
            }
        }
    }

    .menu {
        position: relative;

        @include screen(custom, max, $_mobile_menu_toggle_screen_size) {
            order: 3;
            padding-left: 15px;

            @include screen(custom, max, 430) {
                padding-left: 5px;
            }
        }

        &:after {
            content: '';
            display: block;
            height: 100%;
            width: 100%;
            position: absolute;
            bottom: 0;
            left: 0;
            border-bottom: 1px solid rgba(250,250,250,.45);

            @include screen(custom, max, $_mobile_menu_toggle_screen_size) {
                display: none;
            }
        }

        & > * {
            position: relative;
            z-index: 2;
        }


        .mobile__contact {
            padding: 15px;
            text-align: center;
            display: none;

            @include is_body_class(mob-menu-opened) {
                display: block;
            }

            a {
                @extend .buttonme;
                min-width: 280px;

                span {
                    font-size: 130%;
                }
            }
        }
    }
}