#page-top-image {
	background: $_bg_dark;

	.breadcrumbs-wrapper {
		color: #fff;

		a {
			color: $_color_accent;
		}
	}

	&.image {

		.bg {
			background-size: cover;
			background-color: $_bg_light;
			background-position: center 12%;
			background-repeat: no-repeat;
			@include max-width($_screen_max_width);
		}



		.opacity-lvl {
			
			background: rgba(0,0,0,.55);
			padding: 12% 0 2%;
			height: 80vh;
			@include flexme($direction: column, $space_align: flex-end);

			@include screen(custom, max, 992) {
				padding: 120px 0;
			}
		}

		.container-wrapper {
			background: rgba(0,0,0,.45);
			width: 100%;
			padding: 15px 0;
		}
	}



	&.banner {
		position: relative;

		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 0;
			height: 45%;
			width: 100%;
			transform: translateY(-50%);
			background: $_bg_light;
			z-index: 0;
		}

		& > * {
			position: relative;
			z-index: 2;
		}

		.page-title-wrapper {
			text-align: right;
			padding: 0;
		}


		figure {
			padding: 20px 20px 0 0;
			
			.img-wrapper {
				padding: 50% 0 0 0;
				//overflow: hidden;
				position: relative;
				height: 0;

				&:after {
					content: "";
					display: block;
					position: absolute;
					top: -10px;
					left: 10px;
					border: 1px solid #000;
					width: 100%;
					bottom: -10px;
				}

				img {
					display: block;
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					object-fit: cover;
				}
			}
		}
	}

}