/*
 * Desktop Menu Style
 * STYLE 01
 *
 * 01. Structure
 * 02. Styles
 *
 * id="#site-navigation"
 * & = .menu__main-navigation 
 */

/*
 * local VARs
 */

$_iconic_font_family:   'FontAwesome';      // use here any iconic font and ita value for $_dsk_dropdown_icon
$_dsk_dropdown_icon:    '\f0d7';            //fa-caret-down [&#xf0d7;]

& {
    position: relative;

    /*
     * 01. Structure & Behavior
     * Simple drop-down menu - classic style
     *
     *  [parent] [parent] [parent] [parent]
     *  [child]
     *  [child]
     *  [child][sub-child]
     *          [sub-child]
     *
     */

    menu {

        &.nav-menu {
            position: relative;

            // common props for elems
            li[class*="children"] {
                position: relative;                

                @if $_mt_by_default == false {
                    
                    .sub-menu {
                        position: absolute;
                        //display: none;
                        height: 0;
                        overflow: hidden;
                        opacity: 0;
                        transition: height .2s ease-in;
                        z-index: -2;
                    }

                    &:hover {
                        &.swd-special-dropdown > .sub-menu {
                            //display: block;
                            height: 180px;
                            opacity: 1;
                            @include flexme($hor_align: stretch, $space_align: flex-start);
                            z-index: 20;
                        }

                        &:not(.swd-special-dropdown) > .sub-menu {
                            height: auto;
                            display: block;
                            opacity: 1;
                            z-index: 20;
                        }
                    }
                }
                
            }
            // end common props for elems


            & > li {
                // 1st level menu - root items
                @include d-inline-block;

                & > a {
                    @include d-inline-block;                    
                }

                &[class*="children"] {


                    & > a {
                    
                    }

                    & > .sub-menu-toggler {
                        @include d-inline-block;
                        vertical-align: initial;
                        width: 18px;
                        line-height: 1;

                        &:after {
                            content: $_dsk_dropdown_icon;
                            font-family: $_iconic_font_family;
                        }
                    }

                    & > .sub-menu {
                        // 2nd level menu - sub-menu items
                        top: 100%;
                        left: 0;

                        & > li {

                            & > a {

                            }

                            &[class*="children"] {

                                & > a {
                                    @include d-inline-block;
                                }

                                & > .sub-menu-toggler {
                                    @include d-inline-block;

                                    &:after {
                                        content: $_dsk_dropdown_icon;
                                        font-family: $_iconic_font_family;
                                        transform: rotate(-90deg);
                                        display: block;
                                    }
                                }

                                & > .sub-menu {
                                    // 3rd level menu - sub-sub-menu items
                                    top: 0;
                                    left: 100%;

                                    & > li {

                                        & > a {

                                        }
                                    }
                                } // 3rd lvl sub-menu end
                            } // 3rd li.children end
                        } // li 2nd lvl end
                    } // 2nd sub-menu end
                }// 1st .children end
            }// 1st lvl end
        } // .nav-menu end
    }


    /*
     * 02. Styles Colors, Background, Fonts & Sizes
     */
   menu {

        &.nav-menu {
            text-align: center;

            // common props for elems

            // end common props for elems

            & > li {
                // 1st level menu - root items

                & > a {
                    padding: 10px 15px;
                    color: #fff;
                    font-family: $_font_menu;
                    text-transform: uppercase;
                    font-size: 13px;
                    font-weight: 700;
                    position: relative;

                    @include screen(custom, max, 1510) {
                        padding: 10px 8px;
                        font-size: 12px;
                    }

                    @include screen(custom, max, 1330) {
                        padding: 10px 5px;
                        font-size: 11px;
                    }

                    &:after {
                        content: "";
                        display: block;
                        position: absolute;
                        bottom:0;
                        left: 0;
                        width: 100%;
                        height: 0; 
                        background: #fff;
                        transition: all .4s;
                    }
                }

                &:hover,
                &[class*="current"] {
                    & > a {
                        &:after {
                            height: 2px;

                            @include is_body_class(scrolled) {
                                background: #fff;
                            }
                        }
                    }
                }

                &[class*="current"] {
                    & > a {
                        &:after {
                            //background: $_color_accent;
                            background: #fff;
                        }
                    }
                }

                &[class*="children"] {

                    & > a {
                        
                    }

                    &:not(.swd-special-dropdown) {
                        .sub-menu {
                            width: 150px;
                            left: 50%;
                            transform: translateX(-50%);

                            & > li {

                                & > a {
                                    padding: 8px 15px;
                                    text-align: center;                                    
                                }

                                &:hover,
                                &[class*="current"] {
                                    & > a {
                                        background: $_color_accent;
                                    }
                                }
                            }
                        }
                    }

                    &.swd-special-dropdown {
                        position: static;

                        .sub-menu {
                            padding: 3px;

                            & > li {
                                padding: 3px;

                                /* one item */
                                &:first-child:nth-last-child(1) {
                                /* -or- li:only-child { */
                                    width: 100%;
                                }

                                /* two items */
                                &:first-child:nth-last-child(2),
                                &:first-child:nth-last-child(2) ~ li {
                                    width: 50%;
                                }

                                /* three items */
                                &:first-child:nth-last-child(3),
                                &:first-child:nth-last-child(3) ~ li {
                                    width: 33.3333%;
                                }

                                /* four items */
                                &:first-child:nth-last-child(4),
                                &:first-child:nth-last-child(4) ~ li {
                                    //width: 25%;
                                    width: 33.33%;
                                }

                                /* five items */
                                &:first-child:nth-last-child(5),
                                &:first-child:nth-last-child(5) ~ li {
                                    //width: 25%;
                                    width: 33.33%;
                                }

                                /* five items */
                                &:first-child:nth-last-child(6),
                                &:first-child:nth-last-child(6) ~ li {
                                    //width: 25%;
                                    width: 33.33%;
                                }

                                & > a {
                                    height: 100%;
                                    transition: background .3s;


                                    span.menu-name {
                                        @include flexme;
                                        height: 100%;

                                        .menu__image {
                                            width: 80px;
                                        }

                                        .menu__title {
                                            flex: 1;
                                            padding: 15px;
                                            text-align: left;
                                        }
                                    }
                                }

                                &:hover,
                                &[class*="current"] {
                                    & > a {
                                        background: $_color_accent;
                                    }
                                }
                            }
                        }
                    }

                    & > a {
                        
                    }

                    & > .sub-menu-toggler {
                        
                        &:after {
                            color: #fff;
                            font-size: 10px;
                        }
                    }

                    & > .sub-menu {
                        // 2nd level menu - sub-menu items
                        background: rgba(0,0,0,.85);
                        width: 100%;
                        margin: 0;


                        & > li {
                            display: block;
                            padding: .5px;

                            & > a {
                                display: block;
                                color: #fff;
                                font-size: 14px;
                                line-height: 1.2;
                            }

                            &[class*="children"] {


                                & > a {
                                }

                                & > .sub-menu-toggler {

                                    &:after {

                                    }
                                }

                                & > .sub-menu {
                                    // 3rd level menu - sub-sub-menu items

                                    & > li {

                                        & > a {

                                        }
                                    }
                                } // 3rd lvl sub-menu end
                            } // 3rd li.children end
                        } // li 2nd lvl end
                    } // 2nd sub-menu end
                }// 1st .children end
            }// 1st lvl end
        } // .nav-menu end
    }
}