/*
 * Desktop Menu Style Mobile
 * STYLE 02
 *
 * 01. Structure
 * 02. Styles
 *
 * id="#site-navigation"
 * & = .menu__main-navigation 
 */


/*
 * local VARs
 */

$_iconic_font_family:       'FontAwesome';      // use here any iconic font and ita value for $_dsk_dropdown_icon
$_dropdown_icon:            '\f0d7';            //fa-caret-down [&#xf0d7;]
$_dropdown_icon_toggled:    '\f00d';
$_mobmenu_width:    320px;


& {
    background: $_bg_dark;
    
    position: fixed !important;
    right: 0;
    top: 0;
    z-index: 100;
    height: 100%;
    width: 100%;
    max-width: 430px;
    text-align: center;
    opacity: 0;
    transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transform: scale(0);
    z-index: 10255 !important;
    overflow: auto;


    /*
     * 01. Structure & Behavior
     */

    menu {

        &.nav-menu {
            padding-top: 165px;
            background: url($_img_path + 'olga-hutsul-photography-logo-white.svg') no-repeat center 45px;
            background-size: 60% auto;
            margin: 25px 15px 25px;
            display: block;

            @include screen(custom, max, 400) {
                background-size: 80% auto;
            }            

            // common props for nav elems
            li {
                display: block;
                transition: all 400ms 510ms;
                opacity: 0;

                &:nth-child(odd) {
                    transform: translateX(30%);
                }
                &:nth-child(even) {
                    transform: translateX(-30%);
                }
                &:last-child {
                    transform: none;
                }

                &[class*="children"] {
                    @include flexme;

                    & > a {
                        flex: 1;
                    }

                    & > .sub-menu-toggler {
                        width: 44px;

                        &:after {
                            content: $_dropdown_icon;
                            font-family: $_iconic_font_family;
                        }
                    }

                    .sub-menu {
                        display: none;
                        width: 100%;

                        &.opened {
                            display: block;
                        }
                    }
                }
            }
            // end common props for elems

            & > li {
                // 1st level menu - root items
                transform: translateX($_mobmenu_width);
                -ms-transform: translatex(-$_mobmenu_width); // IE fix

            }// 1st lvl end
        } // .nav-menu end
    }


    &.menu-opened {
        opacity: 1;
        transform: scale(1);

        .nav-menu li {
            opacity: 1;
            transform: translateX(0) !important;
        }
    }

    /*
     * 02. Styles Colors, Background, Fonts & Sizes
     */
   menu {

        &.nav-menu {
            text-align: left;

            // common props for elems

            // end common props for elems

            & > li {
                // 1st level menu - root items
                border-bottom: 1px solid rgba(255,255,255,.15);

                & > a {
                    display: block;
                    padding: 8px 10px;
                    color: #fff;
                    font-size: 16px;
                    text-transform: uppercase;
                }

                &:hover,
                &[class*="current"] {
                    & > a {
                        color: $_color_accent;
                    }
                }


                &[class*="children"] {

                    a {
                        color: #fff;
                        font-size: 15px;
                        text-align: left;
                        display: block;
                    }

                    &:not(.swd-special-dropdown) {
                        .sub-menu {

                            & > li {


                                & > a {
                                    height: 100%;
                                    transition: background .3s;
                                }

                                &:hover,
                                &[class*="current"] {
                                    & > a {
                                        background: $_color_accent;
                                    }
                                }
                            }
                        }
                    }

                    &.swd-special-dropdown {
                        .sub-menu {
                            padding: 0 0 0 15px;

                            & > li {
                                padding: 5px;

                                & > a {
                                    height: 100%;
                                    transition: background .3s;


                                    span.menu-name {
                                        @include flexme;
                                        height: 100%;

                                        .menu__image {
                                            width: 80px;
                                        }

                                        .menu__title {
                                            flex: 1;
                                            padding: 15px;
                                            text-align: left;
                                        }
                                    }
                                }


                                &:hover,
                                &[class*="current"] {
                                    & > a {
                                        background: $_color_accent;
                                    }
                                }
                            }
                        }
                    }

                    & > a {
                        
                    }

                    & > .sub-menu-toggler {
                        text-align: center;
                        box-shadow: 0 0 0 1px rgba(255,255,255,.15);
                        
                        &:after {
                            color: #fff;
                        }

                        &.toggled-on {
                            &:after {
                                content: $_dropdown_icon_toggled;
                            }
                        }
                    }

                    & > .sub-menu {
                        // 2nd level menu - sub-menu items
                        padding: 15px;
                        margin: 0;

                        & > li {

                            & > a {

                            }

                            &[class*="children"] {

                                & > a {
                                }

                                & > .sub-menu-toggler {

                                    &:after {

                                    }
                                }

                                & > .sub-menu {
                                    // 3rd level menu - sub-sub-menu items

                                    & > li {

                                        & > a {

                                        }
                                    }
                                } // 3rd lvl sub-menu end
                            } // 3rd li.children end
                        } // li 2nd lvl end
                    } // 2nd sub-menu end
                }// 1st .children end
            }// 1st lvl end
        } // .nav-menu end
    }
}

