/*
 * Common classes for main elements & servicing styles
 */
body {
	color: $_color_txt;
	position: relative;
	min-width: 320px;
	@extend .customize-scrollbar;// From _scrollbar.scss

	p.tooltip {
		position: absolute;
		display: block;
		padding: 2px 5px;
		background: $_color_txt;
		color: #fff;
		font-family: $_font_heading;
		@include fluid-type($_screen_min_width, $_screen_max_width, 13px, 14px);
		z-index: 100;
		font-weight: 300;
		transform: translateX(-50%);
		transition: all .3s;
	}
}



.title {
	text-transform: uppercase;
	font-family: $_font_heading;
	font-weight: 400;
	line-height: 1.2;

	&.branded {
		
	}
}

.container-fluid {
	@include max-width($_screen_max_width);
}

div.buttonme > a,
p.buttonme > a,
a.buttonme {
	border: 0;
	background: $_color_accent;
	padding: 8px 15px;
	font-family: $_font_button;
	text-align: center;
	@include d-inline-block;
	@include border-radius(8px);
	color: #fff;
	@include fluid-type($_screen_min_width, $_screen_max_width, 15px, 17px);
	text-transform: uppercase;
	transition: background .3s, box-shadow .3s;
	text-decoration: none;

	i {
		margin-right: 8px;
		font-size: 86%;
	}

	&:hover {
		background: $_color_txt;
		box-shadow: 0 0 3px 1px #fff;
	}
}

.services_pull {
	@include flexme($hor_align: stretch, $space_align: flex-start);

	@include screen(custom, max, 1199) {
		padding: 0 15px;
	}

	.ccchildpage {
		width: calc(33.33% - 4px);
		margin: 2px;
		position: relative;
		overflow: hidden;

		@include screen(custom, max, 1199) {
			width: 100%;

			&:not(:last-child) {
				margin: 0 0 15px 0;
			}
		}

		@include screen(custom, max, 500) {
			@include flexme($direction:column-reverse);
		}

		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: -75%;
			z-index: 2;
			display: block;
			width: 50%;
			height: 100%;
			background: -webkit-linear-gradient(left,rgba(255,255,255,0) 0,rgba(255,255,255,.3) 100%);
			background: linear-gradient(to right,rgba(255,255,255,0) 0,rgba(255,255,255,.3) 100%);
			-webkit-transform: skewX(-25deg);
			transform: skewX(-25deg);
		}

		.image {
			position: relative;
			width: 100%;

			.ccpage_linked_thumb {
				width: 100%;
				padding-top: 56.25%;
				overflow: hidden;
				display: block;

				img {
					display: block;
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					object-fit: cover;
				}
			}
		}

		.content {
			position: absolute;
			bottom: 20px;
			left: 0;
			width: 100%;
			z-index: 2;
			background: rgba(0,0,0,.65);
			padding: 15px;
			@include flexme;

			@include screen(custom, max, 500) {
				position: relative;
				bottom: unset;
				left: unset;
				background: $_bg_dark;
			}

			.ccpage_linked_title {
				@extend .title;
				font-weight: 700;
				margin: 0;
				width: calc(100% - 90px);

				.ccpage_title_link {
					display: block;
					line-height: 1.2;
					color: $_color_accent;
					@include fluid-type($_screen_min_width, $_screen_max_width, 16px, 18px);
				}

				&:hover {
					.ccpage_title_link {
						color: #fff;
					}						
				}
			}

			.ccpages_more {
				text-align: right;
				margin-bottom: 0;
				opacity: 0;
				transition: opacity .3s;

				a {
					color: $_color_accent;

					&:hover {
						color: #fff;
					}
				}
			}

			&:hover {
				.ccpages_more {
					opacity: 1;
				}
			}
		}

		&:hover {
			&:after {
				animation: shine .3s;
			}
		}
	}
}


.kt-inside-inner-col > .social-list {
	text-align: left !important;
}

.social-list {
	padding: 0 !important;
	text-align: center;

	& > li {
		@include d-inline-block;
		margin: 5px 10px;
		width: auto !important;

		a {
			display: block;
			width: 33px;
			height: 33px;
			@include border-radius(50%);
			box-shadow: 0 0 0 2px $_color_accent;
			transition: box-shadow .3s, background .3s;

			span {
				font-size: 22px;
				line-height: 1;
				color: $_color_txt;
				display: block;
				text-align: center;
				padding-top: 5px;
				transition: color .3s;
			}
		}

		&:hover {
			a {
				box-shadow: 0 0 0 4px $_color_accent;
				background: $_color_accent;

				span {
					color: #fff;
				}
			}
		}
	}
}

#nav-below {
	@include max-width(1200px);
	margin: 25px auto;
	padding: 15px 0;
	overflow: hidden;
	border-top: 1px solid $_bg_light;
	border-bottom: 1px solid $_bg_light;

	.nav-next,
	.nav-previous {
		padding: 3px;

		a {
			@extend .buttonme;
			display: block;
		}


	}

	ul.page-numbers {

		& > li {
			@include d-inline-block;
		}

		span {
			line-height: 1;
			display: block;
			overflow: hidden;
			@include border-radius(6px);

			&.current {
				background: $_color_accent;
				color: #fff;
			}
		}

		a {
			color: $_color_txt;

			&.page-numbers {
				line-height: 1;
				display: block;
				overflow: hidden;
				@include border-radius(6px);

				&:hover {
					background: $_bg_dark;
					color: #fff;
				}
			}
		}
	}
}