/*
 * Theme typography file
 * 
 * Fonts, Font-sizes, Interlineage, Kerning etc
 */

body {
    @include fluid-type($_screen_min_width, $_screen_max_width, 15px, 16px);
    line-height: $_line_height_norm;
    font-weight: 400;
    font-family: Sans-Serif;
}

html {
    
        body {
            font-family: $_font_base;
        }

        article {
            h1, h2, h3, h4, h5, h6 {
                @extend .title;
            }
        }
    
}


article {

    .entry-content {
        
        .wp-block-kadence-rowlayout:not(.sticked) {

            .kb-section-is-sticky {
                top: 100px;
            }

            .kt-row-column-wrap {
                align-items: center;

                .wp-block-kadence-column {
                    &.kb-section-is-sticky {

                    }
                }
            }
        
        }
        
        .gmp_map_opts {
                padding: 8px;
                background: #fff;
                box-shadow: 0 0 12px 0 rgba(0,0,0,.35);
        }

        .wp-block-image {
            padding: 15px;

            figure {
                padding: 8px;
                background: #fff;
                box-shadow: 0 0 12px 0 rgba(0,0,0,.35);

                figcaption {
                    font-size: 14px;
                    text-align: center;
                }
            }
        }

        .wp-block-embed {
            @include max-width(768px);

            &__wrapper {
                width: 100%;
                position: relative;
                padding-top: 56.25%;

                iframe {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }

        .has-text-align {
            &-center {
                text-align: center;
            }

            &-right {
                text-align: right;
            }

            &-left {
                text-align: left;
            }
        }

        .wp-block-gallery,
        h1, h2, h3, h4, h5, h6 {
            clear: both;
        }

        h1, h2, h3, h4, h5, h6 {
            a {
                color: $_color_accent;
                text-decoration: underline;

                &:hover {
                    text-decoration: none;
                }
            }
        }

        .wp-block-gallery {
            .blocks-gallery-grid {
                margin: 15px auto;
            }
        }

        h4, h5, h6 {
            color: $_color_accent;
        }

        figure[class*="align"] {
            @include screen(custom, max, 600) {
                float: unset !important;
                margin: 0 auto 15px;
                text-align: center;
                width: 100%;
                max-width: 600px;

                img {
                    width: 100%;
                }
            }
        }

        * + h1, * + h2, * + h3, * + h4, * + h5, * + h6 {
            margin-top: 25px;
        }

        li,
        blockquote,
        p:not(.buttonme) {
            margin-bottom: 15px;

            a:not(.buttonme) {
                color: $_color_accent;
                text-decoration: underline;

                &:hover {
                    text-decoration: none;
                }
            }
        }

        ul {
            list-style-type: disc;
        }

        ol {
            list-style-type: decimal;
        }

        ul, ol {
            padding-left: 20px;

            &.two-col-list {

                columns: 2;
                column-gap: 35px;

                @include screen(custom, max, 680) {
                    columns: 1;
                    column-gap: unset;
                }
            }

            &.three-col-list {
                columns: 3;
                column-gap: 35px;

                @include screen(custom, max, 680) {
                    columns: 1;
                    column-gap: unset;
                }
            }

            & > li {
                width: 100%;
            }
        }

        h1 { @include fluid-type($_screen_min_width, $_screen_max_width, 30px, 46px); }
        h2 { @include fluid-type($_screen_min_width, $_screen_max_width, 28px, 40px); }
        h3 { @include fluid-type($_screen_min_width, $_screen_max_width, 26px, 36px); }
        h4 { @include fluid-type($_screen_min_width, $_screen_max_width, 20px, 36px); }
        h5 { @include fluid-type($_screen_min_width, $_screen_max_width, 18px, 30px); }
        h6 { @include fluid-type($_screen_min_width, $_screen_max_width, 18px, 26px); }


        .wp-block-uagb-columns {
            padding: 0;

            .wp-block-uagb-column {
                //min-height: 280px;

                &.has-image {
                    overflow: hidden;
                }

                &:not(.has-image) {
                    padding: 15px;

                    @include screen(custom, max, 976) {
                        padding: 25px 0;
                    }
                }

                .uagb-column__inner-wrap {
                    padding: 0;
                }
            }
        }
    }
}