@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100px) rotate(-50deg);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(-20deg);
    opacity: 1;
  }
}
@keyframes slideInFromRight {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes slideInFromBottom {
  0% {
    top: 150%;
    opacity: 0;
  }
  100% {
    top: 100%;
    opacity: 1;
  }
}
@keyframes opacityOverlayHide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes opacityOverlayZindex {
  0% {
    z-index: 5;
    opacity: 1;
  }
  100% {
    z-index: -1;
    opacity: 0;
  }
}
@keyframes pulsation {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(237, 28, 35, 0.7);
  }
  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(237, 28, 35, 0);
  }
  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(237, 28, 35, 0);
  }
}
@-webkit-keyframes shine {
  100% {
    left: 125%;
  }
}
@keyframes shine {
  100% {
    left: 125%;
  }
}
