#contact-block {
	overflow: hidden;
	border-bottom: 15px solid $_bg_light;
	padding: 0;
	background: $_bg_dark;

	@include is_body_class(inner-page) {
		margin-top: 35px;
	}

	.swifty-img-widget-class {
		@include max-width($_screen_max_width);
	}

	.widget-title {
		@extend .title;
		@include fluid-type($_screen_min_width, $_screen_max_width, 30px, 46px);
		color: $_color_accent;
		text-align: center ;
	}

	.textwidget  {
		& > p {
			color: #fff;
			text-align: center;
		}
	}

	.swifty_imgwidget_ul {
		margin: 0;
		direction: block;

		& > li {
			@include flexme($hor_align: stretch, $direction:row-reverse);

			& > a {
				pointer-events: none;
				position: relative;

				&:before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					right: 0;
					width: 100%;
					height: 100%;
					background: $_bg_dark;
					z-index: 2;
					transition: width .35s ease-out;
				}

				img {
					object-fit: cover;
					display: block;
					width: 100%;
					height: 100%;
					/*
					position: absolute;
					top: 0;
					left: 0;
					*/	
				}
			}

			& > a,
			& > .sbcaption {
				width: 50%;
				display: block;

				@include screen(custom, max, 976) {
					width: 100%;
				}
			}

			& > .sbcaption {
				padding: 4% 3%;
				background: $_bg_dark;

				@include screen(custom, max, 976) {
					padding: 25px 15px 0;
				}
			}
		}
	}

	&.inview {
		.swifty_imgwidget_ul {
			& > li {
				& > a {
					&:before {
						width: 0;
						transition-delay: .15s;
					}
				}
			}
		}
	}
}

.site__footer {

	.footer {
		text-align: center;

		&__logo {

			&__link {
				@include d-inline-block;
				margin-bottom: 35px;
				padding: 15px 45px 35px;
				border-bottom: 1px solid $_bg_light;
			}
		}

		&__social {


		}
	}
}

#copy {
	background: $_bg_dark;

	.site__copyrights__wrapper {
		@include max-width(750px);

		p {
			margin: 0;
			padding: 10px 15px;
			text-align: center;
			font-size: 12px;
			color: #fff;

			a {
				color: $_color_accent;

				&:hover {
					text-decoration: underline;
				}
			}

			.site__copyrights__siteifo {
				@include screen(custom, max, 600) {
					display: block;
				}
			}
		}
	}
}