@import "_includes", "_page-top-image", "_animation-custom";

#primary {
	background: $_bg_light;

	article {
		margin: 15px 0;
		padding: 15px;
		background: #fff;
		box-shadow: 0 0 12px 0 rgba(0,0,0,.35);


		header {
			.feat-image-link {
				display: block;
				height: 0;
				padding-top: 56.25%;
				position: relative;
				overflow: hidden;
				margin-bottom: 15px;

				img {
					width: 100%;
					display: block;
					width: 100%;
					height: 100%;
					object-fit: cover;
					position: absolute;
					top: 0;
					left: 0;
				}
			}

			.entry-title {
				@extend .title;
				text-align: center;
				@include fluid-type($_screen_min_width, $_screen_max_width, 17px, 26px);

				a {
					color: $_color_accent;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}

		main {
			.more-link-wrapper {
				text-align: center;

				a {
					@extend .buttonme;
					min-width: 200px;
				}
			}
		}
	}
}